.search {
  text-align: center;
  height: clamp(80px, 10vh, 90px);
  width: 70%;
  margin: 2% auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: $grey;
  &-standart {
    width: 100%;
    margin-bottom: 1%;
    display: flex;
    align-items: center;
    justify-content: center;
    & .search__input {
      border-radius: 10px 0 0 10px;
      border-right-width: 0;
      width: 84%; }
    & .btn {
      width: 15%;
      margin: -1%;
      height: clamp(35px, 53%, 45px);
      padding: 0.4% 1.5%;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0; } }
  &-advanced {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    height: 0;
    opacity: 0;
    position: absolute;
    top: -100%;
    & .search__input {
       flex-basis: 99%; } }

  &__input {
    color: $grey;
    font-size: 1rem;
    height: clamp(35px, 53%, 45px);
    padding: 1%;
    border: 1px solid $grey;
    border-radius: 10px;
    &:active, &:focus {
      border-color: $blue;
      outline: none; } } }

.rotate-up-icon {
  animation-name: rotate-up-icon;
  animation-duration: 0.4s;
  animation-fill-mode: forwards; }

@keyframes rotate-up-icon {
  0% {
    transform: rotate(0); }
  100% {
    transform: rotate(180deg); } }

.rotate-down-icon {
  animation-name: rotate-down-icon;
  animation-duration: 0.4s;
  animation-fill-mode: forwards; }

@keyframes rotate-down-icon {
  0% {
    transform: rotate(180deg); }
  100% {
    transform: rotate(360deg); } }
