@import 'theme';
@import '../components/_header';
@import '../components/_buttons';
@import '../components/_main-page';
@import '../components/_recipe-page';
@import '../components/_add-recipe';
@import '../components/_pagination';
@import '../components/_search';
@import '../components/_arrow-up';
@import '../components/_modal';
@import '../components/_spinner';
@import '../components/_footer';


html {
  font-size: 16px;
  line-height: 1.5;
  overflow-x: hidden; }

body {
  width: 100vw; }

body.active-modal {
  overflow: hidden;
  position: fixed;
  bottom: 0; }

main {
  position: relative;
  padding: 0 9%;
  margin: 0 0 2%;
  min-height: 50vh; }


section {
  margin-bottom: 1%; }

a {
  text-decoration: none;
  &:hover, &:focus, &:active {
    text-decoration: none; } }


.flex {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

.show-content {
  animation-name: show-content;
  animation-duration: 0.5s;
  animation-fill-mode: forwards; }

@keyframes show-content {
  0% {
    height: 0;
    opacity: 0;
    position: absolute;
    top: -100%; }
  50% {
    opacity: 0.5;
    height: 50%; }
  100% {
    height: 100%;
    opacity: 1;
    position: static; } }

.hide-content {
  animation-name: hide-content;
  animation-duration: 0.5s;
  animation-fill-mode: forwards; }

@keyframes hide-content {
  0% {
    height: 100%;
    opacity: 1;
    position: static; }
  50% {
    opacity: 0.5;
    height: 50%; }
  100% {
    height: 0;
    opacity: 0;
    position: absolute;
    top: -100%; } }
ul {
  list-style: none;
  padding-left: 0; }

.warning {
  color: $grey;
  font-size: 1.1rem;
  text-align: center; }

@import 'media';
