.main-page {
  color: $white;
  .cards {
    justify-content: flex-start;
    column-gap: 1.3%;
    .card {
      position: relative;
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
      flex-basis: 24%;
      height: clamp(330px, 45vh, 450px);
      margin-bottom: 1%;
      text-align: center;
      background-size: cover;
      background-position: center;
      &__substrate {
        width: 100%;
        height: 100%;
        background-color: #000000af;
        position: absolute;
        top: 0;
        left: 0;
        padding: 3%;
        padding-top: 5%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center; }
      &__header {
        margin-bottom: 10%;
        font-weight: 600;
        font-size: 1.4rem;
        flex-basis: 20%;
        ::first-letter {
          text-transform: capitalize; } }
      &__description {
        flex-basis: 45%;
        overflow: hidden;
        text-overflow: ellipsis;
        ::first-letter {
          text-transform: capitalize; } } } }
  a.btn {
    // flex-basis: 20%
    padding: 3% 4.5%;
    font-size: 1rem;
    &:hover {
      color: $white; } }

  .add-fav {
    position: absolute;
    top: 0;
    right: 0; }

  .btn_blue {
    font-size: 1rem; }

  h3::first-letter {
    text-transform: capitalize; }
  p::first-letter {
    text-transform: capitalize; } }

